.counter {
	display: block;
	position: absolute;
	top: 0;
	right: 5px;
	min-width: 20px;
	padding-right: 5px;
	padding-left: 5px;
	color: var(--white);
	background-color: var(--green-400);
	border-radius: 10px;
	font-family: var(--Oswald);
	font-size: 12px;
	font-weight: 600;
	line-height: 20px;
	text-align: center;
	transform: translateX(50%);
}
