.social-share {
	display: flex;
	justify-content: space-between;
	align-items: center;

	&__text {
		display: inline-block;
		flex-shrink: 0;
		margin-right: 5px;
		color: var(--gray-400);
		font-size: 14px;
	}

	&__links {
		display: flex;
	}

	.share-link {
		&:nth-last-child(n + 2) {
			margin-right: 4px;
		}
	}

	.share-link__wrapper {
		position: relative;
		margin-right: 4px;

		.share-link {
			margin-right: 0;
		}
	}
}

@media screen and (max-width: 1199px) {
	.social-share {
		flex-wrap: wrap;

		&__text {
			flex-basis: 100%;
			margin-bottom: 10px;
		}
	}
}
