.category {
	color: var(--gray-700);
	text-decoration: none;
	cursor: pointer;

	.separate-image {
		margin-bottom: 10px;
	}

	&__text {
		display: block;
		padding-right: 6px;
		padding-left: 6px;
		font-size: 14px;
		text-align: center;
		word-wrap: break-word;
		white-space: pre-wrap;
	}

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}
