.checkbox {
	display: flex;
	&__checkbox {
		display: block;
		position: relative;
		flex-shrink: 0;
		width: 20px;
		height: 20px;
		margin-right: 10px;
		border: 2px solid var(--gray-300);
		border-radius: 2px;
		cursor: pointer;
	}

	&__text {
		color: var(--gray-400);
		font-size: 12px;
		line-height: 1.5;
	}

	input[type='checkbox']:checked + .checkbox__checkbox {
		&::after {
			content: '';
			display: block;
			position: absolute;
			top: 2px;
			left: 2px;
			width: 14px;
			height: 8px;
			border-bottom: 2px solid var(--green-400);
			border-left: 2px solid var(--green-400);
			transform: rotate(-45deg);
		}
	}
}
